export default {
	data() {
		return {
			
		}
	},
	methods: {
		mysubmit(e) {
			let usename = e.detail.value.usename;
			let password = e.detail.value.password;
			uni.request({
				url:"https://www.imovietrailer.com/superhero/user/registOrLogin?qq=3129996800",
				data:{
					"username":usename,
					"password":password
				},
				method:"POST",
				success: (res) => {
					//console.log(res.data)
					if(res.data.status == 200){
				
						uni.setStorageSync("globalinfo",res.data.data);
						
						uni.switchTab({
							url:"../me/me"
						})
					}
					else if(res.data.status == 500){
						uni.showToast({
							title:res.data.msg,
							image:"/static/icos/error.png"
						})
					}
				}
			})
		},
		getuserinfo(e){
			let userInfo =e.detail.userInfo;
			let avatarUrl = userInfo.avatarUrl;
			let nickName = userInfo.nickName;
			console.log(avatarUrl);
			console.log(nickName);
			uni.login({
				provider: 'weixin',
				success: (loginRes) => {
					let code = loginRes.code;
					console.log(code);
					uni.request({
						url:"https://www.imovietrailer.com/superhero/stu/mpWXLogin/"+ code,
						data:{
							"avatarUrl":avatarUrl,
							"nickName":nickName,
							"whichMP":1
						},
						method:"POST",
						success: (res) => {
							console.log(res);
							let globalinfo ={
								id:1234567890,
								faceImage:avatarUrl,
								nickname:nickName,
								birthday:"1985.12.14",
								sex:0
							}
							/*
							if(res.data.status == 200){
								uni.setStorageSync("globalinfo",res.data.data);
								uni.switchTab({
									url:"../me/me"
								})
							}
							*/
								uni.setStorageSync("globalinfo",globalinfo);
								uni.switchTab({
									url:"../me/me"
								})							   
						}
					})
				}
			})
		},
		dsfdenglu(e) {
			let denglutype=e.currentTarget.dataset.denglutype;
			//console.log(denglutype);
			uni.login({
				provider:denglutype,
				success: (loginRes) => {
					//console.log(loginRes);
					uni.getUserInfo({
						provider:denglutype,
						success: (res) => {
							//console.log(res.userInfo)
							let userInfo = res.userInfo;
							let face="";
							let nickname="";
							let openIdOrUid="";
							if(denglutype == "weixin"){
								face=userInfo.avatarUrl;
								nickname=userInfo.nickName;
								openIdOrUid=userInfo.openId;
							}else if(denglutype == "qq"){
								face=userInfo.avatarUrl;
								nickname=userInfo.nickname;
								openIdOrUid=userInfo.openId;									
							}else if(denglutype == "sinaweibo"){
								face=userInfo.avatarUrl;
								nickname=userInfo.nickname;
								openIdOrUid=userInfo.id;									
							}
							uni.request({
								url:"https://www.imovietrailer.com/superhero/appUnionLogin/"+denglutype+"?qq=3129996800",
								data:{
									"face":userInfo.avatarUrl,
									"nickname":userInfo.nickName,
									"openIdOrUid":userInfo.openId
								},
								method:"POST",
								success: (loginRes) => {
									//console.log(loginRes.data.data);
									if(loginRes.data.status == 200) {
										uni.setStorageSync("globalinfo",loginRes.data.data);
										uni.switchTab({
											url:"../me/me"
										})
									}
								}
							})
						}
					})
				}
			})
		}
	}
}
